























import { Component, Vue } from 'vue-property-decorator';
import MenuSide from '@/components/menu-side.vue';

interface Menu {
  name: string;
  children: object[];
}

@Component({
  components: {
    MenuSide
  }
})
export default class Solution extends Vue {
  activeParent: number = 0;

  menus: Menu[] = [
    {
      name: '解决方案',
      children: [
        {
          name: '企业通证化改造'
        },
        {
          name: '通证经济激励机制设计'
        },
      ]
    },
    {
      name: '动漫介绍',
      children: [
        {
          name: '海贼王'
        },
        {
          name: '火影忍者'
        },
        {
          name: '犬夜叉'
        },
      ]
    },
  ]

  showMenu(index) {
    this.activeParent = index;
  }
} 
